export default {
  name: "SongSheet",
  props: {
    list: {
      type: String
    },
    perSong: {
      type: Array
    }
  },
  data() {
    return {     
    };
  },
  mounted() {
   
  },
  filters: {
    formatCount (v) {
      if (v < 9999) {
        return v
      } else {
        return (v / 10000).toFixed(0) + '万'
      }
    }
  }
};
